<template>
  <el-container>
    <el-form ref="form" :model="form" label-width="auto" label-position="left" class="bound_fome">
      <el-form-item label="住院号" prop="nbr">
        <el-input v-model="form.nbr" autocomplete="off" placeholder="请输入住院号"></el-input>
      </el-form-item>
      <el-form-item label="姓名" class="bound_fome_name">
        <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
      </el-form-item>
      <el-form-item label="设为默认住院号" label-width="110px" size="medium " class="bound_fome_forthwith">
        <el-switch v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item class="themake_bot" style=" text-align: center;padding-top:17px">
        <!-- <router-link tag="span" to="/shopcart/Prepay/myhospital"> -->
        <el-button type="primary" @click="submitForm('form')">确定绑定</el-button>
        <!-- </router-link> -->
      </el-form-item>
    </el-form>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
export default {
  data() {
    return {
      form: {
        nbr: "1311",
        name: "黄伟",
        delivery: false
      }
    };
  },
  methods: {
    submitForm(formName) {
      // this.$refs[formName].validate(valid => {
      if (this.form.nbr == 1311 && this.form.name === "黄伟") {
        // this.$router.push('/shopcart/Prepay/myhospital');
        // 如果要隐藏参数用params，如果强制刷新不被清除用query
        this.$router.push({
          name: `myhospital`,
          // 传参
          // params: {
          //   nbr: this.form.nbr,
          //   name: this.form.name
          // }
        })
        // 存入vuex
        alert("正确")
        this.$store.commit("uesr",this.form)
      } else {
        alert("填写错误");
        return false;
      }
      // });
    }
  }
};
</script>



